/* eslint-disable jsx-a11y/anchor-is-valid */
import React from "react";
import { Header } from "./containers";
import { Navbar, About, Team, Contact, Footer } from "./Components";
import "./App.css";

const App = () => {
	return (
		<div className="App">
			{/* <!-- Start Preload --> */}
			<div className="preloader">
				<span></span>
			</div>
			{/* <!-- End Preload --> */}
			<div className="navbarBg">
				<Navbar />
				<Header />
			</div>
			<About />
			<Team />
			<Contact />
			<Footer />
			{/* <!-- Toggle Them Start - light and Dark --> */}
			<div className="toggle-them">
				<i className="fas "></i>
			</div>
			{/* <!-- Toggle Them End - light and Dark --> */}
			{/* <!-- Start Scroll Up --> */}
			<a href="#" className="scrollup" id="scroll-up">
				<i className="fas fa-arrow-up scrollup__icon"></i>
			</a>

			{/* <!-- End Scroll Up --> */}
		</div>
	);
};

export default App;
