import React from "react";
import "./contact.css";

function Contact() {
	return (
		<div>
			{/* <!-- Start Contact Section --> */}
			<section
				className="contact section-padding"
				id="contact"
				data-scroll-index="5"
			>
				<div className="container">
					<div className="row justify-content-center">
						<div className="col-lg-8">
							<div className="section-title" data-aos="fade-left">
								<h2>
									Contact <span>US</span>
								</h2>
								<p>
									If U Want To Fast Contact You Can Use This Form Happy For
									Contact We US.
								</p>
							</div>
						</div>
					</div>
					<div className="row">
						<div className="box-contanier">
							<div
								className="contact-info"
								data-aos="fade-right"
								data-aos-delay="100"
							>
								<div>
									<h2>contact info</h2>
									<ul className="info">
										<li>
											<span>
												<img src="./assets/img/contact/location.png" alt="" />
											</span>
											<span>Egypt, Cairo</span>
										</li>
										<li>
											<span>
												<img src="./assets/img/contact/mail.png" alt="" />
											</span>
											<span>
												contact-me@dxd-team.xyz <br /> info@dxd-team.xyz
											</span>
										</li>
										<li>
											<span>
												<img src="./assets/img/contact/call.png" alt="" />
											</span>
											<span>
												+963 935 898 575 <br /> +20 103 084 3276
											</span>
										</li>
									</ul>
								</div>
								<ul className="social">
									<li>
										<a href="#"></a>
										<img src="./assets/img/contact/1.png" alt="" />
									</li>
									<li>
										<a href="#"></a>
										<img src="./assets/img/contact/2.png" alt="" />
									</li>
									<li>
										<a href="#"></a>
										<img src="./assets/img/contact/3.png" alt="" />
									</li>
									<li>
										<a href="#"></a>
										<img src="./assets/img/contact/4.png" alt="" />
									</li>
									<li>
										<a href="#"></a>
										<img src="./assets/img/contact/5.png" alt="" />
									</li>
								</ul>
							</div>
							<div
								className="contact-form"
								data-aos="fade-left"
								data-aos-delay="200"
							>
								<h2>Send a Message</h2>
								<div className="form-box">
									<div className="input-box w-50">
										<input type="text" required />
										<span>First Name</span>
									</div>
									<div className="input-box w-50">
										<input type="text" required />
										<span>Last Name</span>
									</div>
									<div className="input-box w-50">
										<input type="text" required />
										<span>Email Address</span>
									</div>
									<div className="input-box w-100">
										<textarea required></textarea>
										<span>Write Your Message Here</span>
									</div>
									<div className="input-box mb-0 w-50">
										<input
											className="btn-1"
											type="submit"
											value="Send Message"
										/>
									</div>
								</div>
							</div>
						</div>
					</div>
				</div>
			</section>
			{/* <!-- End Contact Section --> */}
		</div>
	);
}

export default Contact;
