import React from "react";
import "./header.css";

function Header() {
	return (
		<div className="dxd__header">
			{/* <!-- Start Home Section --> */}
			<div
				className="home d-flex align-items-center"
				id="home"
				data-scroll-index="0"
			>
				<div className="container">
					<div className="align-items-center">
						<div className="text-center center-sec">
							<div
								className="home-text"
								data-aos="zoom-in"
								data-aos-delay="150"
							>
								<h1>
									welcome to <span>DXD-TEAM</span> site
								</h1>
								<p>
									We Are The DXD Team, We Are Distinguished In Creating
									Responsive And <br />
									Wonderful Web Content As Well As Clean Code And Speed In Work
									And Delivery
								</p>
								<div className="home-btn">
									<a href="#team" className="btn btn-1">
										Get Started
									</a>
								</div>
							</div>
						</div>
					</div>
				</div>
			</div>
			{/* <!-- End Home Section --> */}
		</div>
	);
}

export default Header;
